* {
    box-sizing: border-box;
}

body {
    margin: 0;
    font-family: -apple-system,BlinkMacSystemFont,"Segoe UI",Roboto,"Helvetica Neue",Arial,sans-serif;
    font-weight: 400;
    line-height: 1.5;
    color: #212529;
    background-color: #eee;
}

h1, h2, h3 {
    color: #333;
}

h1 {
    font-size: 40px;
    font-weight: bold;
    color: rgba(51, 51, 51, 0.8);
    text-shadow: 1px 4px 6px #fff, 0 0 0 #000, 1px 4px 6px #fff;
    margin-top: 38px;
}

::-moz-selection {
    background: #5af;
    color: #fff;
    text-shadow: none;
}

::selection {
    background: #5af;
    color: #fff;
    text-shadow: none;
}

.checkbox, .radio {
    margin-top: 20px;
    margin-bottom: 20px;
    line-height: 165%;
}

.checkbox input[type=checkbox],
.checkbox-inline input[type=checkbox],
.radio input[type=radio],
.radio-inline input[type=radio] {
    margin-left: -30px;
}

.checkbox label, .radio label {
    padding-left: 30px;
}

.tab {
    margin-left: 40px;
}

header,
#shadow {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    height: 170px;
    background: #fff;
    z-index: 2;
}

#hot tabletable:first-of-type,
#shadow {
    border-color: #cccccc;
    z-index: -1;
}

header {
    border-style: solid;
    border-width: 1px;
    border-color: #cccccc;
}

header h1,
header #logos {
    display: inline-block;
}

header #logos {
    float: right;
    line-height: 170px;
}

header #logos img:not(:first-child) {
    margin-left: 50px;
}

.bg-adequate {
    background-color: var(--adequate-color);
}

.bg-unclear {
    background-color: var(--unclear-color);
}

.bg-inadequate {
    background-color: var(--inadequate-color);
}

.bg-invalid {
    background-color: var(--invalid-color);
}

@media (min-width: 992px) and (max-width: 1200px) {
    h1 {
        font-size: 35px;
        margin-top: 42px;
    }
}

@media (min-width: 768px) and (max-width: 992px) {
    h1 {
        font-size: 30px;
        margin-top: 50px;
    }

    header #logos img {
        display: block;
        width: 50%;
        margin-top: 20px;
    }

    header #logos img:not(:first-child) {
        margin-left: 0;
    }
}

@media (max-width: 768px) {
    h1 {
        font-size: 30px;
        margin-top: 50px;
    }

    header #logos img {
        display: none;
    }
}

#content {
    margin-top: 270px;
}

/*#hot,*/
.card {
    -webkit-border-radius: 0px;
    -moz-border-radius: 0px;
    border-radius: 0px;
    border-color: #cccccc !important;
}

#instructions {
    transition: width 1s, margin-left 1s;
}

#expand-table-button {
    width: 100%;
    height: 35px;
    display: block;
    margin-top: 35px;
}

#expand-table-button:hover {
    background-color: #F3F3F3;
}

#tables-area h4 {
    margin-top: 30px;
}

hr {
    display: block;
    width: 100%;
    border-top: 1px solid #d6d6d6;
    margin-top: 30px;
    margin-bottom: 30px;
}

hr.short {
    width: 30%;
}

form .form-group-lg label {
    font-size: 18px;
}

.panel-heading h3 {
    text-align: center;
}

.panel-body {
    padding-left: 1em;
    padding-right: 1em;
}

.instructions {
    font-size: large;
}

.page-footer {
    padding-top: 50px;
}

/* for drop area */

#drop-area {
    border: 3px dashed #CCC;
    border-radius: 8px;
    padding: 30px;
    background-color: #F0F0F0;
    transition: border-color 0.5s;
    text-align: center;
    display: block;
    vertical-align: middle;
    font-size: 22px;
    width: 100%;
}

#drop-area p {
    font-size: 13px;
    margin-top: 25px;
}

#drop-area button {
    margin-top: -25px;
    padding-left: 25px;
    padding-right: 25px;
    border-radius: 20px;
}

#drop-area.dragenter {
    border-color: #A6DE2A;
}

#drop-area.dragdefault {
    border-color: #666;
}

#choose-file {
    border: none;
    visibility: hidden;
}

/* Additional Questions */
#questions,
#review-and-submit form {
    font-size: 16px;
}

.question-text {
    font-weight: 500;
}

/* for alerts and submission history*/
.ajs-header img {
    padding-right: 0.5em;
}

.success img {
    padding-right: 0.5em;
}

.error img {
    padding-right: 0.5em;
}

/* for qtip */
.qtip-red {
    color: #5B1515;
    background-color: #FDD3D0;
}

.qtip-red img {
    padding-right: 0.5em;
}

.qtip-yellow {
    color: #5b5914;
    background-color: #fdfad0;
}

.qtip-yellow img {
    padding-right: 0.5em;
}

#errors {
    display: none;
}

#validation-errors li,
#submission-history li {
    margin-bottom: 10px;
}

span.error {
    color: #F04744;
}

span.success {
    color: #5cb85c;
}

#submit {
    margin-top: 20px;
}

/* Triangles, based on https://tympanus.net/codrops/2013/10/03/a-collection-of-separator-styles/ */
#shadow::after {
    position: absolute;
    content: '';
    pointer-events: none;
}

.ss-style-multitriangles::after {
    left: 50%;
    width: 50px;
    height: 50px;
    -webkit-transform: translateX(-50%) rotate(45deg);
    transform: translateX(-50%) rotate(45deg);
}

.ss-style-multitriangles::after {
    bottom: -22px;
    z-index: 1;
    background: inherit;
    box-shadow: -50px 50px 0 #cccccc, 50px -50px 0 #cccccc, 2px 2px 0 0 #cccccc, -48px 52px 0 0 #cccccc, 52px -48px 0 0 #cccccc;
}

/* *** */

.arrow-down {
    background: transparent url('./images/expand.png') no-repeat center;
    cursor: pointer;
}

.flip {
    transform: scaleY(-1);
}
